export function getPrices(
  prices,
  price,
  computePerPack,
  productPackVariantMap,
) {
  if (
    computePerPack &&
    productPackVariantMap &&
    Object.keys(productPackVariantMap).length > 0
  ) {
    return prices.map(priceObj => {
      if (productPackVariantMap[priceObj?.shopifyId])
        return {
          ...priceObj,
          price:
            priceObj?.price /
            productPackVariantMap[priceObj?.shopifyId]?.packQuantity,
        }
      return priceObj
    })
  }
  return prices || price
}
