const TransparentPricing = ({
  title,
  pricing,
  pricingListJson,
  pricingTitle,
  quincePrice,
  traditionalRetailPrice,
  selectedVariantId,
  transparentPricingIcons,
  showTitle = true,
  className,
}) => {
  const customSortOrder = [
    'Materials',
    'Crafting Cost',
    'Packaging',
    'Freight & Handling',
    'Credit Card Fees',
  ]

  const applicableCostingParts = {
    crafting: 'Crafting Cost',
    materials: 'Materials',
    packaging: 'Packaging',
    creditCardFees: 'Credit Card Fees',
    shippingHandling: 'Freight & Handling',
  }

  let pricingJson = null
  if (pricingListJson) {
    for (let i = 0; i < pricingListJson.length; i++) {
      if (
        pricingListJson[i] &&
        pricingListJson[i].shopifyId === parseInt(selectedVariantId)
      ) {
        const parts = Object.keys(pricingListJson[i])
        pricingJson = []
        for (let j = 0; j < parts.length; j++) {
          if (Object.keys(applicableCostingParts).indexOf(parts[j]) > -1) {
            pricingJson.push({
              name: parts[j],
              price: pricingListJson[i][parts[j]],
            })
          }
        }
        break
      }
    }
  }

  const totalCost = pricingJson
    ? pricingJson.reduce((acc, val) => acc + val.price, 0)
    : pricing
    ? pricing.reduce((acc, val) => acc + val.price, 0)
    : 0

  const computeHeight = (height, price, cost) => {
    return (height * price) / cost
  }

  let baseHeight = 100
  const subtitleWords = pricingTitle && pricingTitle.split(' ')
  const newQuincePrice =
    quincePrice && quincePrice > 0 ? quincePrice - totalCost : null
  const newTraditionalRetailPrice =
    traditionalRetailPrice && traditionalRetailPrice > 0
      ? traditionalRetailPrice - totalCost
      : null
  let quinceHeight = newQuincePrice
    ? computeHeight(baseHeight, newQuincePrice, totalCost)
    : null
  let trpHeight =
    newTraditionalRetailPrice != null
      ? computeHeight(baseHeight, newTraditionalRetailPrice, totalCost)
      : null

  // Re-adjust scales, if the ratio of prices is too high
  if (
    quincePrice &&
    traditionalRetailPrice &&
    quincePrice > 0 &&
    traditionalRetailPrice > 0
  ) {
    if (traditionalRetailPrice / quincePrice > 4) {
      baseHeight = 40
      quinceHeight = computeHeight(baseHeight, newQuincePrice, totalCost)
      trpHeight = computeHeight(
        baseHeight,
        newTraditionalRetailPrice,
        totalCost,
      )
    }
  }

  // Sort them in a specific order
  const pricingWithCustomOrder = []
  if (pricingJson) {
    for (let i = 0; i < customSortOrder.length; i++) {
      for (let j = 0; j < pricingJson.length; j++) {
        const pricingKey = pricingJson[j].name
        if (applicableCostingParts[pricingKey] === customSortOrder[i]) {
          const isIconPresent =
            transparentPricingIcons.filter(
              item => item.description === pricingKey,
            ).length > 0
          pricingWithCustomOrder.push({
            name: applicableCostingParts[pricingKey],
            price: pricingJson[j].price,
            icon: isIconPresent
              ? transparentPricingIcons.filter(
                  item => item.description === pricingKey,
                )[0]
              : null,
          })
          break
        }
      }
    }
  } else if (pricing) {
    for (let i = 0; i < customSortOrder.length; i++) {
      for (let j = 0; j < pricing.length; j++) {
        const pricingKey = pricing[j].name
        if (pricingKey === customSortOrder[i]) {
          pricingWithCustomOrder.push(pricing[j])
          break
        }
      }
    }
  }

  return (
    <StyledTransparentPricing className={className}>
      {showTitle && <Title>{title}</Title>}
      <Container className='transparent__pricing-container'>
        <Wrapper>
          <Costing className='transparent__pricing-costing'>
            <Subtitle>
              {pricingTitle &&
                subtitleWords.map((word, index) => {
                  if (index === 0) {
                    return <span key={`${word}${index}`}>{word}</span>
                  }
                  return (
                    <React.Fragment key={`${word}${index}`}>
                      {word}
                    </React.Fragment>
                  )
                })}
            </Subtitle>
            <CostList>
              {pricingWithCustomOrder.map((item, index) => {
                const itemPrice = item.price.toFixed(2)
                return (
                  <ListItem key={item.id + index}>
                    {item.icon && (
                      <IconWrapper>
                        <img
                          className={item.name
                            .split('&')
                            .join('-')
                            .split(' ')
                            .join('-')
                            .toLowerCase()}
                          src={item.icon.file.url}
                          alt={item.name}
                        />
                      </IconWrapper>
                    )}
                    <ItemTitle>{item.name}</ItemTitle>
                    <ItemPrice>${itemPrice}</ItemPrice>
                  </ListItem>
                )
              })}
            </CostList>
            <TotalCost>
              <p>Total cost</p>
              <span>${totalCost.toFixed(2).toString()}</span>
            </TotalCost>
          </Costing>
          {quincePrice && (
            <RelativePricing>
              <Column>
                <ColumnPrice>
                  <span>$</span>
                  {parseFloat(quincePrice).toFixed(2)}
                </ColumnPrice>
                <ColumnVariable
                  style={{
                    '--heightMobile': `${quinceHeight}px`,
                    '--heightDesktop': desktopVw(quinceHeight),
                  }}
                />
                <ColumnBase
                  style={{
                    '--maxHeightMobile': `${baseHeight}px`,
                    '--maxHeightDesktop': desktopVw(baseHeight),
                  }}
                >
                  Total cost
                </ColumnBase>
                <ColumnTitle>
                  <Logo />
                </ColumnTitle>
              </Column>
              {traditionalRetailPrice && traditionalRetailPrice > 0 ? (
                <Column>
                  <ColumnPrice>
                    <span>$</span>
                    {parseFloat(traditionalRetailPrice).toFixed(2)}
                  </ColumnPrice>
                  <ColumnVariable
                    style={{
                      '--heightMobile': `${trpHeight}px`,
                      '--heightDesktop': desktopVw(trpHeight),
                    }}
                  />
                  <ColumnBase
                    style={{
                      '--maxHeightMobile': `${baseHeight}px`,
                      '--maxHeightDesktop': desktopVw(baseHeight),
                    }}
                  >
                    Total cost
                  </ColumnBase>
                  <ColumnTitle>Traditional retail</ColumnTitle>
                </Column>
              ) : null}
            </RelativePricing>
          )}
        </Wrapper>
      </Container>
    </StyledTransparentPricing>
  )
}

export default withErrorBoundary(TransparentPricing, {
  fallbackRender: () => <div />,
})
