import React, { useEffect, useState } from 'react'
import styled from 'styled-components'

import GreenCheckBox from '../../assets/green_check_box.svg'
import RedCross from '../../assets/red_cross_box.svg'
import BlackCheckbox from '../../assets/black_check_box.svg'

import { desktopVw } from '../../styles/utils'
import { withErrorBoundary } from '../ErrorBoundary/ErrorBoundary'
import { getPrices } from '../../utils/comparisonPricing'

const StyledComparisonPricing = styled.div`
  font-size: 11px;
  margin: 28px 0;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    display: none;
    font-size: ${desktopVw(11)};
    margin: ${desktopVw(20)} 0 0;
    padding: 0;
  }
`

const Title = styled.label`
  text-transform: uppercase;
  padding: 0 18px;
  font-size: 11px;
  letter-spacing: 0.2px;

  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    padding: 0;
    font-size: ${desktopVw(11)};
    font-weight: 400;
    font-family: ${props => props.theme.fonts.primary};
  }
`

const StyledTable = styled.table`
  width: 100%;
  border-collapse: collapse;
  margin-top: 22px;
  td:nth-child(2) {
    font-weight: 500;
    font-size: 13px;
    text-transform: uppercase;
    background: ${props => props.theme.colors.lightPink};
  }
  tr:nth-child(1) {
    border-bottom: ${props => `1px solid ${props.theme.colors.primary}`};

    > td {
      border-top-left-radius: 12px;
      border-top-right-radius: 12px;
    }
  }
  tr:last-child > td {
    border-bottom-left-radius: 12px;
    border-bottom-right-radius: 12px;
  }
`

const RowHeader = styled.td`
  height: 60px;
  width: 75px;
  font-size: 11px;
  font-weight: 500;
  text-align: center;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: 50px;
    width: 102px;
    text-align: left;
  }
`

const ColumnHeader = styled.td`
  height: 43px;
  width: 100px;
  font-size: 11px;
  text-transform: uppercase;
  text-align: center;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    height: 35px;
  }
`

const StyledName = styled.div`
  margin: 0 5px;
`

const Cell = styled.td`
  height: 50px;
  width: 90px;
  text-align: center;
  font-size: 11px;
`

const ReturnsText = styled.div`
  text-transform: uppercase;
  margin: 0 30px;
  @media (min-width: ${props => props.theme.breakpoints.desktopSmall}px) {
    margin: 0;
  }
`

const SavingsText = styled(Cell)`
  color: ${props => props.theme.colors.red};
`

const DataRow = styled.tr`
  border-bottom: ${props => `1px solid ${props.theme.colors.greyLighter}`};
  &:last-child {
    border-bottom: none;
  }
`

function computeNormalizedPricing(
  pricingItems,
  computePerPack,
  productPackVariantMap,
) {
  pricingItems.sort((a, b) => a.price - b.price)

  const normalizedPricing = pricingItems
    .filter(priceObj => priceObj.name.toLowerCase() !== 'quince')
    .slice(0, 2)
    .reduce(
      (accObj, { name, prices, price, features }) => {
        accObj.names.push(name)
        accObj.prices.push(
          getPrices(prices, price, computePerPack, productPackVariantMap),
        ) // Prices - JSON, Price - ContentType
        accObj.productFeatures.push(features)
        return accObj
      },
      { names: [], prices: [], productFeatures: [] },
    )
  return normalizedPricing
}

const ComparisonPricingMobile = ({
  quincePrice,
  pricing,
  pricingJson,
  showTitle = true,
  selectedVariantId,
  computePerPack,
  productPackVariantMap,
}) => {
  const [normalizedPricing, setNormalizedPricing] = useState(
    computeNormalizedPricing(pricingJson?.items ?? []),
  )
  const [pricingObjectType, setPricingObjectType] = useState('json')

  /**
   * Pricing Json is currently flowing from PC UI
   * TODO: Remove Pricing after migration from contentful to PC UI
   */

  useEffect(() => {
    let pricingItems

    if (pricingJson && Object.keys(pricingJson).length > 0)
      pricingItems = pricingJson.items
    else if (pricing && pricing.length > 0) {
      pricingItems = pricing
      setPricingObjectType('content')
    } else return

    const normalized = computeNormalizedPricing(
      pricingItems,
      computePerPack,
      productPackVariantMap,
    )
    setNormalizedPricing(normalized)
  }, [pricing, pricingJson, quincePrice])

  if (!normalizedPricing) return null

  const { names = [], prices = [], productFeatures = [] } =
    normalizedPricing || {}

  const arrayReducer = (a, b) => a.concat(b)
  const shopifyIdMatcher = ({ shopifyId }) => shopifyId == selectedVariantId
  const checkIfDataAvailable = prices =>
    prices.reduce(arrayReducer, []).filter(shopifyIdMatcher).length > 0
  const computePercentage = (num, denm) =>
    Math.round(((num - denm) / num) * 100)
  const variantFilter = () =>
    prices.reduce(arrayReducer, []).filter(shopifyIdMatcher)

  const priceRenderer = () => {
    return pricingObjectType === 'json'
      ? checkIfDataAvailable(prices) &&
          variantFilter().map((priceObject, index) => (
            <Cell key={`${selectedVariantId + index}-price`}>
              {priceObject.price &&
                `$${parseFloat(priceObject.price).toFixed(2)}`}
            </Cell>
          ))
      : prices.map((price, index) => (
          <Cell key={index}>{price && `$${parseFloat(price).toFixed(2)}`}</Cell>
        ))
  }

  const percentageRenderer = () => {
    return pricingObjectType === 'json'
      ? checkIfDataAvailable(prices) &&
          variantFilter().map((priceObject, index) => (
            <SavingsText key={`${selectedVariantId + index}-percentage`}>
              {priceObject.price &&
                quincePrice &&
                `${computePercentage(priceObject.price, quincePrice)}%`}
            </SavingsText>
          ))
      : prices.map((price, index) => (
          <SavingsText key={index}>
            {price &&
              quincePrice &&
              `${computePercentage(price, quincePrice)}%`}
          </SavingsText>
        ))
  }

  const freeShippingRenderer = () => {
    return pricingObjectType == 'json'
      ? checkIfDataAvailable(prices) &&
          variantFilter().map((priceObject, index) =>
            priceObject.freeShipping === false ? (
              <Cell>
                <RedCross
                  key={`${selectedVariantId + index}-no-free-returns`}
                />
              </Cell>
            ) : (
              <Cell>
                <BlackCheckbox
                  key={`${selectedVariantId + index}-yes-free-returns`}
                />
              </Cell>
            ),
          )
      : names.map((name, index) => (
          <Cell key={`${name}-${index}`}>
            <BlackCheckbox />
          </Cell>
        ))
  }

  const returnsRenderer = () => {
    return pricingObjectType == 'json'
      ? checkIfDataAvailable(prices) &&
          variantFilter().map((priceObject, index) =>
            priceObject.freeReturns ? (
              <Cell key={index}>
                <ReturnsText>{priceObject.freeReturns} days</ReturnsText>
              </Cell>
            ) : (
              <Cell>
                <ReturnsText> 30 Days </ReturnsText>
              </Cell>
            ),
          )
      : names.map((name, index) => (
          <Cell key={index}>
            <ReturnsText>30 days</ReturnsText>
          </Cell>
        ))
  }

  return (
    <StyledComparisonPricing pricing={pricing} pricingJson={pricingJson}>
      {showTitle && <Title>Comparison pricing</Title>}

      <StyledTable>
        <tbody>
          <tr>
            <td />
            <ColumnHeader>QUINCE</ColumnHeader>
            {names.map(name => (
              <ColumnHeader key={name}>
                <StyledName>{name}</StyledName>
              </ColumnHeader>
            ))}
          </tr>

          <DataRow>
            <RowHeader>Price</RowHeader>
            <Cell>
              {quincePrice && `$${parseFloat(quincePrice).toFixed(2)}`}
            </Cell>
            {priceRenderer()}
          </DataRow>
          <DataRow>
            <RowHeader>Your Savings</RowHeader>
            <Cell>
              <GreenCheckBox />
            </Cell>
            {percentageRenderer()}
          </DataRow>
          {pricingJson &&
            pricingJson.features &&
            pricingJson.features.map((feature, indexA) => {
              const featureName = feature.name
              const extractedWords = featureName.split(' ')
              const extractedWordsSize = extractedWords.length
              return (
                <DataRow>
                  <RowHeader>
                    <div>
                      {extractedWords.map((text, index) => {
                        return index < extractedWordsSize / 2 ? (
                          <span>{text} </span>
                        ) : null
                      })}
                    </div>
                    <div>
                      {extractedWords.map((text, index) => {
                        return index >= extractedWordsSize / 2 ? (
                          <span>{text} </span>
                        ) : null
                      })}
                    </div>
                  </RowHeader>
                  <Cell>
                    <GreenCheckBox />
                  </Cell>
                  {productFeatures.map((productFeature, indexB) => (
                    <Cell key={`${indexA} - ${indexB}`}>
                      {productFeature &&
                      productFeature.indexOf(featureName) > -1 ? (
                        <BlackCheckbox />
                      ) : (
                        <RedCross />
                      )}
                    </Cell>
                  ))}
                </DataRow>
              )
            })}
          <DataRow>
            <RowHeader>Free Shipping</RowHeader>
            <Cell>
              <GreenCheckBox />
            </Cell>
            {freeShippingRenderer()}
          </DataRow>
          <DataRow>
            <RowHeader>Free Returns</RowHeader>
            <Cell>
              <ReturnsText>365 days</ReturnsText>
            </Cell>
            {returnsRenderer()}
          </DataRow>
        </tbody>
      </StyledTable>
    </StyledComparisonPricing>
  )
}

export default withErrorBoundary(ComparisonPricingMobile, {
  fallbackRender: () => <div />,
})
